import React, {Component} from 'react'
import {bindActionCreators} from 'redux'
import {connect} from 'react-redux'
import * as actions from '../actions'
import 'formbase/dist/formbase.min.css'
import 'react-select/dist/react-select.css'
import "./style.less"
import ContentTable from '../component/ContentTable'
import User from '../component/UserComponent'

class App extends Component {
    constructor(props) {
        super(props)
        this.state={
            table:true,
            edit:false,
            arr:[]
        }
    }
    handleAddUser(){
        this.setState({table:false,edit:false});
        this.props.actions.userFentchRoleList({})
    }
    handleEditUser(){
        this.setState({table:false,edit:true});
        this.props.actions.userFentchRoleList({})
    }
    render() {
        return (
            this.state.table?
                <div id="user" className="clearfix">
                    <ContentTable
                        user={this.props.user}
                        actions={this.props.actions}
                        edit={this.state.edit}
                        handleAddUser={()=>this.handleAddUser()}
                        handleEditUser={()=>this.handleEditUser()} />
                </div>
            :
                <div id="user" className="clearfix">
                    <User
                        ref="user"
                        user={this.props.user}
                        actions={this.props.actions}
                        edit={this.state.edit}
                        handleCancel={()=>this.setState({table:true})}/>
                </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        user: state.user
    };
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        actions: bindActionCreators(actions, dispatch)
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(App)



