import React, {Component} from 'react'
import Typography from 'material-ui/Typography';
import Modal from 'material-ui/Modal'
import 'ztree/js/jquery-1.4.4.min'
import 'ztree/js/jquery.ztree.core'
import 'ztree/css/zTreeStyle/zTreeStyle.css'
import Spin from '@/components/spin'
export default class RoleModal extends Component {
    load=()=>{
        const setting = {
            data: {
                simpleData: {
                    enable: true
                }
            }
        };
        $.fn.zTree.init($(".roletree ul"), setting, this.props.data);
    }
    componentDidMount(){
        $("#roleMoadl .content .detail").mCustomScrollbar({
            scrollInertia: 250,
            theme: 'dark'
        });
    }
    render() {
        const { handleClose,roles,fetching } = this.props
        return (
            <Modal
                aria-labelledby="simple-modal-title"
                aria-describedby="simple-modal-description"
                open={this.props.open}
                onClose={this.handleClose}
                onRendered={
                    ()=>{this.load()}
                }
            >

                <div id="roleMoadl">
                    <Spin loading={fetching}>
                        <div className="modalHeader">/<span className="close" onClick={()=>handleClose()}>X</span></div>
                        <div className="modalBody" style={{paddingTop:'10px'}}>
                            <div className="left">
                                <div className="title">{roles.data?roles.data.length:0}</div>
                                <div className="detail">
                                    {
                                        roles.data?roles.data.map((item,i)=>{
                                            return(
                                                <div key={i} className="item">{item.roleName}</div>
                                            )
                                        }):''
                                    }
                                </div>
                            </div>
                            <div className="content">
                                <div className="title"></div>
                                <div className="detail" style={{height:'408px',overflow:'auto'}}>
                                    <Typography className="roletree" component="div" style={{ paddingTop:'20px' }}>
                                        <ul className="ztree" ref="ztree"></ul>
                                    </Typography>
                                </div>
                            </div>
                        </div>
                    </Spin>
                </div>

            </Modal>

        );
    }
}



